<template>
  <div style="width:100%;height:100%;position: relative">
    <Select v-model="selectAct" style="width:200px;position: absolute;right:30px;top:10px;z-index: 111">
      <Option :value="1">年份</Option>
      <Option :value="2">类型</Option>
      <Option :value="3">行政区划</Option>
    </Select>
    <div id="myChart" ref="chart" style="width:100%;height:100%" v-resize="redraw"></div>
  </div>
</template>

<script>
import { totalByType } from '@/api/reportAnalysis'
export default {
  name: 'hello',
  data () {
    return {
      myChart: {},
      datas: {},
      selectAct: 1
    }
  },
  computed: {
    xAxisData: function () {
      let Arr = []
      for (let item of this.datas) {
        Arr.push(item.name)
      }
      return Arr
    },
    count: function () {
      let Arr = []
      for (let item of this.datas) {
        Arr.push(item.count)
      }
      return Arr
    }
  },
  mounted () {
    this.getTableData1()
  },
  watch: {
    selectAct () {
      this.getTableData1()
    }
  },
  methods: {
    /* 获取按类型统计数量数据 */
    getTableData1 () {
      totalByType({
        data: {
          type: this.selectAct
        }
      }).then((res) => {
        if (res.data.code === '0000') {
          this.datas = res.data.data
          this.drawLine()
        }
      }).catch(() => {
        this.$Notice.error({ title: '服务器错误' })
      })
    },
    /* 获取字段列表 */
    dataHandle () {
      this.datas = this.data
      this.drawLine()
    },
    drawLine () {
      // 基于准备好的dom，初始化echarts实例
      this.myChart = this.$echarts.init(this.$refs.chart)
      // 绘制图表
      this.myChart.setOption({
        title: {
          text: '人类活动信息统计',
          x: '15px',
          top: 10
        },
        grid: {
          left: '5%',
          right: '5%',
          bottom: '7%',
          top: '80px',
          containLabel: true
        },
        // legend: {
        //   data: ['台账数量', '违法违规台账数量', '已销号台账数量', '未销号台账数量'],
        //   top: 30,
        //   right: 60
        // },
        tooltip: {
          trigger: 'axis'
        },
        xAxis: {
          type: 'category',
          data: this.xAxisData,
          axisLabel: {
            interval: 0,
            formatter: (value, index) => {
              if (index % 2 !== 0) {
                return '\n' + value
              } else {
                return value
              }
            }
          }
        },
        yAxis: [
          {
            type: 'value',
            name: '数量/个',
            minInterval: 1,
            min: 0,
            axisLabel: {
              formatter: '{value}'
            }
          }
        ],
        series: [
          {
            // name: '台账数量',
            type: 'bar',
            z: 3,
            itemStyle: {
              normal: {
                color: 'rgb(212,130,101)'
              }
            },
            data: this.count,
            barMaxWidth: 30
          }
        ]
      })
      // 窗口改变时图表自适应
      window.addEventListener('resize', () => this.myChart.resize(), false)
    },
    redraw () {
      if (this.myChart.id !== undefined) {
        this.myChart.resize()
      }
    },
    // 返回64位字节码
    back64bit () {
      return this.myChart.getDataURL({
        type: 'jpeg',
        pixelRatio: 1,
        backgroundColor: '#fff'
      })
    }
  }
}
</script>

<style lang="scss" rel="stylesheet/scss" scoped>

</style>
