<template>
  <div id="myChart" ref="chart" style="width:100%;height:100%" v-resize="redraw"></div>
</template>

<script>
export default {
  name: 'hello',
  data () {
    return {
      myChart: {},
      datas: {}
    }
  },
  computed: {
    legendData: function () {
      let Arr = []
      for (let item of this.datas) {
        if (item.type !== null && item.type !== '' && item.type !== undefined) {
          Arr.push(item.type)
        }
      }
      return Arr
    },
    pidData: function () {
      let Arr = []
      for (let item of this.datas) {
        Arr.push(item.area)
      }
      return Arr
    },
    seriesData: function () {
      let Arr = []
      for (let i in this.datas) {
        let obj = {
          name: this.legendData[i],
          value: this.pidData[i]
        }
        Arr.push(obj)
      }
      return Arr
    }
  },
  props: {
    /* 编辑行原数据 */
    data: {}
  },
  watch: {
    data () {
      this.dataHandle()
    }
  },
  methods: {
    /* 获取数据 */
    dataHandle () {
      this.datas = this.data
      this.drawLine()
    },
    drawLine () {
      // 基于准备好的dom，初始化echarts实例
      this.myChart = this.$echarts.init(this.$refs.chart)
      // 绘制图表
      this.myChart.setOption({
        title: {
          text: '按人类活动类型统计面积占比',
          x: '15px',
          top: 10
        },
        tooltip: {
          trigger: 'item',
          formatter: '{b} : {c} ({d}%)'
        },
        legend: {
          orient: 'vertical',
          left: 20,
          top: 50,
          data: this.legendData
        },
        series: [
          {
            type: 'pie',
            radius: '45%',
            center: ['50%', '60%'],
            data: this.seriesData,
            itemStyle: {
              emphasis: {
                shadowBlur: 10,
                shadowOffsetX: 0,
                shadowColor: 'rgba(0, 0, 0, 0.5)'
              }
            }
          }
        ]
      })
      // 窗口改变时图表自适应
      window.addEventListener('resize', () => this.myChart.resize(), false)
    },
    redraw () {
      if (this.myChart.id !== undefined) {
        this.myChart.resize()
      }
    },
    // 返回64位字节码
    back64bit () {
      return this.myChart.getDataURL({
        type: 'jpeg',
        pixelRatio: 1,
        backgroundColor: '#fff'
      })
    }
  }
}
</script>

