import request from '@/utils/request'

/* 按类型统计数量数据 */
export const getCountGroupByType = data => request({ url: '/ygjc/image/getCountGroupByType', method: 'post', data })
/* 按类型统计面积数据 */
export const getAreaGroupByType = data => request({ url: '/ygjc/image/getAreaGroupByType', method: 'post', data })
export const getAreaChange = data => request({ url: '/ygjc/image/getAreaChange', method: 'post', data })
export const getCountChange = data => request({ url: '/ygjc/image/getCountChange', method: 'post', data })
export const totalByType = data => request({ url: '/ygjc/image/totalByType', method: 'post', data })
// 人类活动批次列表
export const getList = data => request({ url: '/ygjc/image/list', method: 'post', data })
// 导出统计
export const exportReport = data => request({ url: '/ygjc/image/exportZTTJ', method: 'post', data })
