<template>
  <div id="areaManagement-wrapper">
    <div style="line-height: 40px;text-align: left;padding-left: 15px;padding-top: 10px">
      <span style="font-size: 16px;margin-right: 10px">人类活动批次：</span>
      <Button type="success" size="small" @click="activitiesModal = true" :loading="loading" v-model="activitiesValue"  v-html="activitiesContent" style="margin-right: 10px"></Button>
      <div style="position: absolute;top: 10px; right: 10px">
        <Button @click="clickFabu">推送</Button>
        <Button type="success" @click="exportchart" style="margin-top: -3px">导出</Button>
      </div>
    </div>
    <div class="statistical">
      <transition name="fade">
        <div class="sta_box" :class="{activeBox: boxstate === 1}" style="left:0;top:0;padding: 15px 5px 5px 15px">
          <div class="con_box">
            <Button :icon="boxstate === 1 ? 'md-contract' : 'md-expand'" @click="boxstate = boxstate === 1 ? 0 :1" size="small" class="boxBtn"></Button>
            <div class="bigBox" :class="{smallToBig:boxstate === 1}">
              <div class="smallBox">
                <BarChart1 :data="getBarData1" ref="BarChart1"></BarChart1>
              </div>
            </div>
            <Spin size="large" fix v-if="spinShow1"></Spin>
          </div>
        </div>
      </transition>
      <div class="sta_box" :class="{activeBox: boxstate === 2}" style="left:50%;top:0;padding:15px 15px 5px 5px">
        <div class="con_box">
          <Button :icon="boxstate === 2 ? 'md-contract' : 'md-expand'" @click="boxstate = boxstate === 2 ? 0 :2" size="small" class="boxBtn"></Button>
          <div class="bigBox" :class="{smallToBig:boxstate === 2}">
            <div class="smallBox">
              <PieChart1 :data="getPieData1" ref="PieChart1"></PieChart1>
            </div>
          </div>
          <Spin size="large" fix v-if="spinShow2"></Spin>
        </div>
      </div>
      <div class="sta_box" :class="{activeBox: boxstate === 3}" style="left:0;top:50%;padding: 5px 5px 15px 15px">
        <div class="con_box">
          <Button :icon="boxstate === 3 ? 'md-contract' : 'md-expand'" @click="boxstate = boxstate === 3 ? 0 :3" size="small" class="boxBtn"></Button>
          <div class="bigBox" :class="{smallToBig:boxstate === 3}">
            <div class="smallBox2">
              <BarChart2 :data="getBarData2" ref="BarChart2"></BarChart2>
            </div>
            <div class="smallBox2">
              <BarChart3 ref="BarChart3"></BarChart3>
            </div>
          </div>
          <Spin size="large" fix v-if="spinShow3"></Spin>
        </div>
      </div>
      <div class="sta_box" :class="{activeBox: boxstate === 4}" style="left:50%;top:50%;padding: 5px 15px 15px 5px">
        <div class="con_box">
          <Button :icon="boxstate === 4 ? 'md-contract' : 'md-expand'" @click="boxstate = boxstate === 4 ? 0 :4" size="small" class="boxBtn"></Button>
          <div class="bigBox" :class="{smallToBig:boxstate === 4}">
            <div class="smallBox">
              <table1 :id="curid" ref="PieChart2"></table1>
            </div>
          </div>
          <Spin size="large" fix v-if="spinShow4"></Spin>
        </div>
      </div>
    </div>
    <!-- 人类活动列表-->
    <Modal
      v-model="activitiesModal"
      title="人类活动批次列表"
      @on-cancel="handleCancel"
      width="1000">
      <div>
        <Table border stripe :loading="aLoading" :columns="aColumns1"
               :data="aData1" highlight-row :height="aTableHeight"
               ref="activitiesMultipleTable"
               @on-current-change="changeAccount"
               id="reportAnalysisTable"></Table>
        <div class="page_box" style="width: 100%;padding: 15px;text-align: right">
          <Page :total="aTotal" :page-size="aPageSize" :current.sync="aPageNum" @on-change="aPageChange" show-total/>
        </div>
      </div>
      <div slot="footer">
        <i-button type="text" @click="handleCancel" class="mr10">取消</i-button>
        <Button type="primary" @click="selectActivities">确定</Button>
      </div>
    </Modal>
    <Modal
      v-model="modal1"
      title="推送消息时间">
      <Form :model="tuisongdata"  :label-width="120">
        <FormItem label="发布时间" prop="time">
          <DatePicker type="date" v-model="tuisongdata.time" placeholder="Select date"></DatePicker>
        </FormItem>
      </Form>
      <div slot="footer">
        <Button @click="tuisong">确定</Button>
      </div>
    </Modal>
  </div>
</template>

<script>
import BarChart1 from './BarChart1.vue'
import BarChart2 from './BarChart2.vue'
import BarChart3 from './BarChart3.vue'
import PieChart1 from './PieChart1.vue'
import PieChart2 from './PieChart2.vue'
import table1 from './Table.vue'
import {
  // 按类型统计数量数据
  getCountGroupByType,
  // 按类型统计面积数据
  getAreaGroupByType,
  getAreaChange,
  getCountChange,
  getList,
  exportReport
} from '@/api/reportAnalysis'
import { msgsetList, st4ScsCbdAdd } from '@/api/systemManage'
import moment from 'moment'
export default {
  name: 'dataAnalysis',
  data () {
    return {
      /* 放大块的标识 */
      boxstate: 0,
      // 柱状图数据
      barChartData: {},
      // 统计图数据
      getBarData1: [],
      getPieData1: [],
      getBarData2: [],
      getPieData2: [],
      // 统计图数据加载状态
      spinShow1: true,
      spinShow2: true,
      spinShow3: true,
      spinShow4: true,
      // 选中人类活动批次id
      activitiesValue: '',
      // 选中人类活动批次name
      activitiesContent: '请选择人类活动批次',
      // 人类活动列表弹窗
      activitiesModal: false,
      /* 表格高度 */
      aTableHeight: Math.floor((window.innerHeight - 300 - 70) / 48) * 48 + 40,
      /* 表格数据 */
      aColumns1: [
        // {
        //   type: 'selection',
        //   width: 60,
        //   align: 'center'
        // },
        {
          title: '序号',
          key: 'index',
          width: 100,
          align: 'center',
          render: (h, params) => {
            return h('span', params.index + (this.aPageNum - 1) * this.aPageSize + 1)
          }
        },
        {
          title: '任务名称',
          align: 'center',
          minWidth: 200,
          key: 'name'
        },
        {
          title: '说明',
          align: 'center',
          minWidth: 250,
          key: 'remark'
        },
        {
          title: '底图服务',
          align: 'center',
          width: 300,
          key: 'url'
        }
      ],
      aData1: [],
      /* 总条数 */
      aTotal: 0,
      aPageNum: 1,
      aPageSize: Math.floor((Math.floor((window.innerHeight - 300 - 40) / 48) * 48 + 40) / 48),
      /* 加载 */
      aLoading: false,
      // 人类活动多选相关参数
      selectActiveRow: [],
      multipleActiveSelection: [],
      /* 当前选中表格行数据 */
      currentRow: null,
      curid: 0,
      loading: false, // 导出按钮的
      msgset: {}, // 存储发布配置
      tuisongdata: {
        time: new Date()
      },
      modal1: false // 弹框控制
    }
  },
  components: {
    BarChart1,
    BarChart2,
    BarChart3,
    PieChart1,
    table1
  },
  watch: {
    // 人类活动多选
    multipleActiveSelection (data) { // 存储选中的row
      this.selectActiveRow = []
      if (data.length > 0) {
        data.forEach((item, index) => {
          this.selectActiveRow.push(item.id)
        })
      }
    }
  },
  created () {
    this.getTableData1()
    this.getTableData2()
    this.getTableData3()
    this.getTableData4()
    /* 获取详情信息 */
    this.getTableList()
    this.msgsetList()
  },
  methods: {
    /* 获取按类型统计数量数据 */
    getTableData1 (e) {
      getCountGroupByType({
        data: {
          imageId: e
        }
      }).then((res) => {
        if (res.data.code === '0000') {
          this.spinShow1 = false
          // 第一个柱状统计图
          this.getBarData1 = []
          if (res.data.data.length > 0) {
            res.data.data.forEach((item, index) => {
              // 统计图loading
              this.spinShow1 = false
              this.getBarData1.push({
                type: item.type,
                count: item.count
              })
            })
          } else {
            this.spinShow1 = false
          }
        }
      }).catch(() => {
        this.$Notice.error({ title: '服务器错误' })
      })
    },
    /* 获取按类型统计面积数据 */
    getTableData2 (e) {
      getAreaGroupByType({
        data: {
          imageId: e
        }
      }).then((res) => {
        if (res.data.code === '0000') {
          this.spinShow1 = false
          // 第二个饼状统计图
          this.getPieData1 = []
          if (res.data.data.length > 0) {
            res.data.data.forEach((item, index) => {
              // 统计图loading
              this.spinShow2 = false
              this.getPieData1.push({
                type: item.type,
                area: item.area
              })
            })
          } else {
            this.spinShow2 = false
          }
        }
      }).catch(() => {
        this.$Notice.error({ title: '服务器错误' })
      })
    },
    /* 获取按类型统计数量数据 */
    getTableData3 () {
      getCountChange({
        data: {}
      }).then((res) => {
        if (res.data.code === '0000') {
          this.spinShow3 = false
          // 第三个柱状统计图
          this.getBarData2 = []
          if (res.data.data.length > 0) {
            res.data.data.forEach((item, index) => {
              // 统计图loading
              this.spinShow3 = false
              this.getBarData2.push({
                type: item.name,
                count: item.count
              })
            })
          } else {
            this.spinShow3 = false
          }
        }
      }).catch(() => {
        this.$Notice.error({ title: '服务器错误' })
      })
    },
    /* 获取按类型统计面积数据 */
    getTableData4 () {
      getAreaChange({
        data: {}
      }).then((res) => {
        if (res.data.code === '0000') {
          this.spinShow4 = false
          // 第二个饼状统计图
          this.getPieData2 = []
          if (res.data.data.length > 0) {
            res.data.data.forEach((item, index) => {
              // 统计图loading
              this.spinShow4 = false
              this.getPieData2.push({
                type: item.name,
                area: item.area
              })
            })
          } else {
            this.spinShow4 = false
          }
        }
      }).catch(() => {
        this.$Notice.error({ title: '服务器错误' })
      })
    },
    /* axios: 获取人类活动列表数据 */
    getTableList () {
      this.aLoading = true
      getList({
        data: {
          pageSize: this.aPageSize,
          pageNum: this.aPageNum
        }
      }).then((res) => {
        if (res.data.code === '0000') {
          this.aData1 = res.data.data.rows
          if (this.aData1.length > 0) {
            this.aData1[0]._highlight = true
          }
          this.activitiesContent = this.aData1[0].name
          this.curid = this.aData1[0].id
          this.getTableData1(this.aData1[0].id)
          this.getTableData2(this.aData1[0].id)
          this.getTableData3(this.aData1[0].id)
          this.getTableData4(this.aData1[0].id)
          this.aTotal = res.data.data.total
          this.aLoading = false
        }
      }).catch(() => {
        this.$Notice.error({ title: '服务器错误' })
      })
    },
    /* 人类活动列表分页跳转 */
    aPageChange (pageNum) {
      this.aPageNum = pageNum
      this.getTableList()
    },
    // 勾选人类活动checkbox事件
    handleSelectionChange (val) {
      this.multipleActiveSelection = val
    },
    /* 取消选择人类活动 */
    handleCancel () {
      this.$refs.activitiesMultipleTable.clearCurrentRow()
      this.activitiesModal = false
      // this.selectActiveRow = []
      // this.multipleActiveSelection = []
    },
    // 保存勾选人类活动
    selectActivities () {
      this.activitiesContent = this.currentRow.name
      this.activitiesValue = this.currentRow.id
      this.curid = this.currentRow.id
      this.getTableData1(this.currentRow.id)
      this.getTableData2(this.currentRow.id)
      this.handleCancel()
    },
    /* 选中台账的函数 */
    changeAccount (currentRow, oldCurrentRow) {
      this.currentRow = currentRow
    },
    /* 导出报告 */
    exportchart () {
      console.log(this.$refs.BarChart1.back64bit())
      let BarChart1 = this.$refs.BarChart1.back64bit().slice(23)
      let PieChart1 = this.$refs.PieChart1.back64bit().slice(23)
      let BarChart2 = this.$refs.BarChart2.back64bit().slice(23)
      exportReport({
        data: {
          data1: BarChart1,
          data2: PieChart1,
          data3: BarChart2
        }
      }).then(res => {
        this.loading = false
        if (res.data.code === '0000') {
          window.open(res.data.data)
        } else {
          this.$Notice.error({ title: '导出报告失败' })
        }
      })
    },
    // 推送配置
    msgsetList () {
      msgsetList({
        data: {}
      }).then(res => {
        if (res.data.code === '0000') {
          this.msgset = res.data.data.rows[0]
        } else {
          this.$Notice.error({ title: '获取' })
        }
      })
    },
    // 点击推送消息
    clickFabu () {
      if (this.msgset.time) {
        this.modal1 = true
      } else {
        this.tuisong()
      }
    },
    // 推送信息
    tuisong () {
      this.modal1 = false
      st4ScsCbdAdd({
        data: {
          time: moment(this.tuisongdata.time).format('YYYY-MM-DD'),
          user: '系统用户',
          midContent: '发布了新的专题统计分析报告',
          afterContent: '发布了新的专题统计分析报告',
          users: this.msgset.user,
          verify: this.msgset.verify ? 1 : 3
        }
      }).then(res => {
        if (res.data.code === '0000') {
          this.$Notice.success({ title: '新建消息提醒成功' })
          this.tuisongdata = {
            time: new Date()
          }
        } else {
          this.$Notice.error({ title: res.data.msg })
        }
      })
    }
  }
}
</script>

<style lang="scss" rel="stylesheet/scss" type="text/scss" scoped>
  #areaManagement-wrapper{
    .statistical{
      position: relative;
      width: 100%;
      height: calc(100% - 62px);
      .sta_box{
        position: absolute;
        height: 50%;
        width: 50%;
        padding:15px 20px;
        .con_box{
          height: 100%;
          width: 100%;
          background: #fff;
          border-radius: 10px;
          box-shadow: 0 0 5px 3px rgba(0,0,0,0.1);
          position: relative;
          overflow: hidden;
          .boxBtn{
            position: absolute;
            right: 10px;
            top:10px;
            z-index: 80;
          }
          .bigBox{
            width:calc(100vw - 60px);
            height:100%;
            .smallBox{
              width:50%;
              height:100%;
              float: left;
            }
            .smallBox2{
              width:50%;
              height:100%;
              float: left;
            }
          }
          .smallToBig {
            .smallBox {
              width: 100%;
            }
            .smallBox2 {
              width: 100%;
              height: 50%;
            }
          }
        }
      }
      /* box放大 */
      .activeBox{
        left:0!important;
        top:0!important;
        padding: 15px !important;
        width: 100% !important;
        height: 100%;
        z-index: 99;
        transition: all 0.8s ease;
      }
    }
    #humanPc {
      padding: 15px 15px 0px;
      .ivu-form-item {
        margin: 0;
      }
    }
  }
</style>
