<template>
  <div id="myChart" ref="chart" style="width:100%;height:100%" v-resize="redraw"></div>
</template>

<script>
export default {
  name: 'hello',
  data () {
    return {
      myChart: {},
      datas: {}
    }
  },
  computed: {
    xAxisData: function () {
      let Arr = []
      for (let item of this.datas) {
        if (item.type === null || item.type === '' || item.type === undefined) {
          item.type = ''
        }
        Arr.push(item.type)
      }
      return Arr
    },
    count: function () {
      let Arr = []
      for (let item of this.datas) {
        Arr.push(item.area)
      }
      return Arr
    }
  },
  props: {
    /* 编辑行原数据 */
    data: {}
  },
  watch: {
    data () {
      this.dataHandle()
    }
  },
  methods: {
    /* 获取字段列表 */
    dataHandle () {
      this.datas = this.data
      this.drawLine()
    },
    drawLine () {
      // 基于准备好的dom，初始化echarts实例
      this.myChart = this.$echarts.init(this.$refs.chart)
      // 绘制图表
      this.myChart.setOption({
        title: {
          text: '人类活动解译按批次统计面积',
          x: '15px',
          top: 10
        },
        grid: {
          left: '5%',
          right: '5%',
          bottom: '7%',
          top: '80px',
          containLabel: true
        },
        // legend: {
        //   data: ['台账数量', '违法违规台账数量', '已销号台账数量', '未销号台账数量'],
        //   top: 30,
        //   right: 60
        // },
        tooltip: {
          trigger: 'axis'
        },
        xAxis: {
          type: 'category',
          data: this.xAxisData,
          axisLabel: {
            interval: 0,
            formatter: (value, index) => {
              if (index % 2 !== 0) {
                return '\n' + value
              } else {
                return value
              }
            }
          }
        },
        yAxis: [
          {
            type: 'value',
            name: '面积/km²',
            minInterval: 1,
            min: 0,
            axisLabel: {
              formatter: '{value}'
            }
          }
        ],
        series: [
          {
            // name: '台账数量',
            type: 'bar',
            z: 3,
            itemStyle: {
              normal: {
                color: 'rgb(116,159,131)'
              }
            },
            data: this.count,
            barMaxWidth: 30
          }
        ]
      })
      // 窗口改变时图表自适应
      window.addEventListener('resize', () => this.myChart.resize(), false)
    },
    redraw () {
      if (this.myChart.id !== undefined) {
        this.myChart.resize()
      }
    },
    // 返回64位字节码
    back64bit () {
      return this.myChart.getDataURL({
        type: 'jpeg',
        pixelRatio: 1,
        backgroundColor: '#fff'
      })
    }
  }
}
</script>

