<template>
  <div id="atable">
    <div style="height:calc(100% - 0px);overflow: auto">
      <Table :columns="columns1" :data="detailData" highlight-row></Table>
    </div>
  </div>
</template>
<script>
import { getAudit } from '@/api/remoteSensing'
export default {
  name: 'hello',
  data () {
    return {
      /*  */
      columns1: [
        {
          title: '序号',
          align: 'center',
          width: 100,
          render: (h, params) => {
            return h('span', params.index + 1)
          }
        },
        {
          title: '红线斑块名称',
          align: 'center',
          key: 'hxcode'
        },
        {
          title: '人类活动面积',
          align: 'center',
          key: 'insectarea'
        },
        {
          title: '总面积',
          align: 'center',
          key: 'hxarea'
        }],
      detailData: [],
      activitiesModal: false,
      actList: '',
      dataList: [],
      aPageNum: 1,
      aPageSize: 10,
      aTotal: 0,
      title: ''
    }
  },
  props: {
    /* 编辑行原数据 */
    id: {}
  },
  watch: {
    id (val) {
      this.getMapTableList(val)
    }
  },
  methods: {
    /* 获取详情信息 */
    async getMapTableList (id) {
      await getAudit({
        data: {
          id: id.toString()
        }
      }).then(res => {
        if (res.data.code === '0000') {
          if (res.data.data.out === '') {
            this.detailData = []
          } else {
            let features = JSON.parse(res.data.data.out).value.features
            let arr = []
            for (let item of features) {
              arr.push(item.attributes)
            }
            this.detailData = arr
          }
        } else {
          /* 错误信息弹框 */
          this.$Notice.error({ title: '获取对比详情失败', desc: res.data.msg })
        }
      })
    }
  }
}
</script>

<style lang="scss" rel="stylesheet/scss" scoped>
  #atable{
    height: 100%;
  }
</style>
<style lang="scss">
  #highlight{
    .ivu-table-row-highlight td{
      background-color: #2d8cf0!important;
      color: #fff!important;
    }
  }
</style>

